@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(0, 43, 58);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    cursor: pointer;
}
header {
    display: flex;
    background: rgb(221, 221, 221);
    /* background: black; */
    justify-content: space-around;
    padding: 10px;
    height: 30px;
}

#logo {
    height: 100%;
    width: 180px;
}

.search {
    width: 400px;
}

#searchInput {
    width: 80%;
    margin-right: -100px;
}

#searchButton {
    width: 15%;
    margin-left: 50px;
}

img {
    color: rgb(65, 64, 64);
}
.loggedUserHeaderImg {
    height: 80%;
    width: 20px;
    margin-left: -10px;
}
.container {
    display: flex;
    flex-wrap: wrap;
}

.user {
    position: relative;
    width: 30%;
}

.userBlock {
    display: flex;
    margin: 10px;
    height: 95px;
    border-bottom: 1px solid rgb(94, 94, 94);
    padding: 5px;
}

.profileImage {
    height: 70px;
    width: 70px;
    position: relative;

}

.profileImage:hover {
    z-index: 5;
    height: 80px;
    width: 80px;
}

.userInfo {
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 12px;
    padding: 0px 5px 5px 5px;
}

.tags {
    margin: 5px 0 ;
}

.userPreviewBlock {
    display: block;
    z-index: 2;
    position: absolute;
    top: 10px;
    left: 10px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    overflow: hidden;
    width: 120%;
    background: rgb(161, 161, 161);
    border-radius: 5px;
} 

.generalInfoPreview {
    width: 100%;
    margin: 10px;
    padding: 5px;
    margin-left: 80px;
}

.previewImage {
    height: 70px;
    width: 70px;
}

.userInfoPreview {
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 12px;
    padding: 0px 5px 5px 5px;
}

.aboutPreview {
    font-size: 12px;
}
.userTag {
    margin-right: 6px;
    position: relative;
}

.userTag:not(:last-child):after {
    content: ',';
    position: absolute;
    right: -2px;
    top: 0;
    display: block;
    height: 2px;
    width: 2px;
}


.tagBlock {
    color: white;
    padding: 5px;
    border: 1px solid  rgb(94, 94, 94);
    width: 22%;
    margin: 5px;
    font-size: 12px;
}

.tagName {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 5px;
    border-radius: 5px;
    color: black;
    background: lightskyblue;
}

.numOfQuestions {
    width: 20%;
    display: flex;
    flex-wrap: wrap;
    color: rgb(179, 179, 179);
}
main {
    display: grid;
    /* justify-content: space-between; */
    grid-template-columns: 20% 70%;
    /* grid-template-rows: 100%; */
    margin-top: 20px;
    grid-template-areas: 'sideBar mainField';
}

#sideBar {
    grid-area: sideBar;
    width: 100%;
    /* margin: 20px; */
    /* margin-top: 20px; */
    background: rgb(0, 43, 58);
    margin: 0 20px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 10px;
    border-left: 1px solid black;
}

#mainField {
    grid-area: mainField;
    width: 100%;
    margin: 0 50px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    background: rgb(0, 43, 58);
    padding: 10px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 15px rgba(0,0,0,0.5);
}
nav {
    display: flex;
    flex-direction: column;
    text-align: center;
    list-style-type: none;
    margin-left: -40px;
}

nav li {
    margin: 10px;
    text-decoration: none;
}

nav a {
    color: rgb(221, 221, 221);
    text-decoration: none;
}

nav a:hover {
    cursor: pointer;
}
.sorting {
    display: flex;
    justify-content: flex-end;
    color: white;
}

.sortby {
    align-self: center;
    margin-right: 10px;
}

.sortOption {
    padding: 0 10px;
    width: 120px;
    text-align: center;
}

.sortOrder {
    display: flex;
    justify-content: space-between;
} 

.sortOrder:hover {
    cursor: pointer;
}

.sortOrder span {
    border: 1px solid rgb(94, 94, 94);
    padding: 5px;
    width: 50%;
}


.questionBlock {
    display: flex;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 5px;
    color: white;
    border-bottom: 1px solid rgb(94, 94, 94);
}

.votes {
    grid-area: votes;
}

.answers {
    grid-area: answers;
}

.views {
    grid-area: views;
}

.questionTitle {
    grid-area: questionTitle;
    padding: 10px;
    /* width: max-content;
    overflow-wrap: ; */
}

.questionTitle:hover {
    text-decoration: underline;
}

.theQuestion {
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: 100%;
    width: 80%;
    grid-template-areas: 'questionTitle' 'tags';
    padding: 5px;
}

.tags {
    grid-area: tags;
    /* padding: 10px; */
    display: flex;
    flex-wrap: wrap;
    margin: 5px;
}

.tag {
    /* padding: 5px; */
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 5px;
    margin: 5px;
    background: grey;
    border-radius: 5px;
}

.tag:hover {
    cursor: pointer;
}

.ownerInfo {
    margin-left: auto;
    padding: 10px;
}

.questionOwner {
    color: rgb(129, 127, 250);
}

.questionOwner:hover {
    text-decoration: underline;
}

.views, .answers, .votes {
    /* margin-top: 5px; */
    text-align: center;
    padding: 10px;
    margin: auto 10px;
    height: 100%;
}

.answered {
    border: 1px solid green;
    color: green;
    border-radius: 10px;
}

.hasAcceptedAnswer {
    background: green;
    color: white;
    border-radius: 10px;
}

a {
    color: white;
    text-decoration: none;
}
.theQuestionBlock {
    color: white;
    padding-bottom: 20px;
    border-bottom: 1px solid rgb(94, 94, 94);
    margin-bottom: 30px;
}

.headOfBlock {
    border-bottom: 1px solid rgb(94, 94, 94);
    padding: 0 20px 20px 20px;
    margin-bottom: 20px;
}

.dates {
    margin-top: 15px;
    color: rgb(165, 165, 165);
}

.when {
    text-decoration: underline;
    padding: 5px;
}

.questionBody {
    display: flex;

}

.voting {
    display: flex;
    flex-direction: column;
    width: 8%;
    text-align: center;
}

.voteButton {
    height: 60px;
    width: 60%;
    margin: 10px auto;
}

.sideButton {
    height: 20px;
    width: 30%;
    margin: 5px auto;
}

.questionText {
    margin-top: 10px;
    margin-left: 20px;
    width: 80%;
}

.textOfQuestion pre {
    overflow: auto;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    max-height: 500px;
    width: 100%;
    background: rgb(231, 207, 180);
    font-family: 'Source Code Pro', monospace;
    color: black;
}

.tagsOfQuestion {
    display: flex;
    flex-wrap: wrap;
    margin: 5px;
    margin-top: 10%;
}

.tagOfQuestion {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 5px;
    margin: 5px;
    background: grey;
    border-radius: 5px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.ownerOfQuestionInfo {
    margin-left: auto;
    display: flex;
    height: 60px;
    background: rgb(194, 194, 194);
    border-radius: 10px;
    color: rgb(0, 43, 58);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.userOfQuestionPic {
    height: 50px;
    width: 50px;
    margin: auto 0;
    padding: 5px;
}

.genInfo {
    font-size: 12px;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.numOfAnswers {
    color: white;
    text-align: left;
    padding-left: 15px;
}

.comments {
    margin-top: 15px;
}
.answerBlock {
    color: white;
    padding-bottom: 20px;
    border-bottom: 1px solid rgb(94, 94, 94);

}

.answerBody {
    display: flex;
}

.voting {
    display: flex;
    flex-direction: column;
    width: 8%;
    text-align: center;
}

.voteButton {
    height: 60px;
    width: 60%;
    margin: 5px auto;
}

.sideButton {
    height: 20px;
    width: 30%;
    margin: 5px auto;
}

.answerText {
    margin-top: 10px;
    margin-left: 20px;
    width: 80%;
    text-align: left;
}

.textOfAnswer pre {
    overflow-x: scroll;
    background: rgb(231, 207, 180);
    font-family: 'Source Code Pro', monospace;
    color: black;
}

.ownerOfAnswerInfo {
    margin-top: 10%;
    margin-left: auto;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    background: rgb(194, 194, 194);
    border-radius: 10px;
    color: rgb(0, 43, 58);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.dateOfAnswer {
    font-size: 10px;
    padding-left: 5px;
}

.moreOwnerInfo {
    display: flex;
}

.userOfAnswerPic {
    height: 50px;
    width: 50px;
    margin: auto 0;
    padding: 5px;
}

.genInfo {
    font-size: 12px;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.accepted {
    height: 40px;
    width: 50%;
    margin: 5px auto;
}

.notAccepted {
    display: none;
}

.userOfAnswerName {
    color: rgb(0, 86, 117);
}
.comment {
    background: white;
    color: black;
    border-bottom: 1px solid black;
    padding: 5px;
    font-size: 11px;
}

.comment a{
    color: rgb(94, 94, 255);
    text-decoration: underline;
}

.comment span {
    margin: 0 5px;
}

.authorName {
    color: rgb(94, 94, 255);
}
.userPage {
    color: white;
}

.mainUserPageInfo {
    display: flex;
    margin-top: 20px;
}

.mainUserPageInfo div {
    margin: 10px;
}

.userPicandInfo {
    width: 25%;
    padding-top: 20px;
}

.userPageGenInfo {
    width: 70%;
}

.userPageUserProfInfo {
    align-self: center;
    border-left: 1px solid white;
    padding: 10px;
    width: 20%;
    overflow-wrap: break-word;
}

.userPagePost {
    border-bottom: 1px solid rgb(94, 94, 94);
    width: 100%;
    margin: 20px 10px;
}

.userPostDate {
    text-align: right;
    margin-right: 15px;
}

.userPageNav {
    display: flex;
    flex-direction: row;
    margin: 20px;
}

.userPageNav li {
    padding: 5px;
    background: rgb(94, 94, 94);
    border-radius: 10px;
}

.userPageNav li:hover {
    cursor: pointer;
}

.bronzeBadge,
.silverBadge,
.goldBadge {
    position: relative;
    margin: 10px;
}

.bronzeBadge::before {
    content: '';
    height: 10px;
    width: 10px;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 5px;
    left: -10px;
    background: rgb(165, 97, 42);
}

.silverBadge::before {
    content: '';
    height: 10px;
    width: 10px;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 5px;
    left: -10px;
    background: rgb(167, 167, 167);
}

.goldBadge::before {
    content: '';
    height: 10px;
    width: 10px;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 5px;
    left: -10px;
    background: rgb(255, 242, 56);
}
