.userPage {
    color: white;
}

.mainUserPageInfo {
    display: flex;
    margin-top: 20px;
}

.mainUserPageInfo div {
    margin: 10px;
}

.userPicandInfo {
    width: 25%;
    padding-top: 20px;
}

.userPageGenInfo {
    width: 70%;
}

.userPageUserProfInfo {
    align-self: center;
    border-left: 1px solid white;
    padding: 10px;
    width: 20%;
    overflow-wrap: break-word;
}

.userPagePost {
    border-bottom: 1px solid rgb(94, 94, 94);
    width: 100%;
    margin: 20px 10px;
}

.userPostDate {
    text-align: right;
    margin-right: 15px;
}

.userPageNav {
    display: flex;
    flex-direction: row;
    margin: 20px;
}

.userPageNav li {
    padding: 5px;
    background: rgb(94, 94, 94);
    border-radius: 10px;
}

.userPageNav li:hover {
    cursor: pointer;
}

.bronzeBadge,
.silverBadge,
.goldBadge {
    position: relative;
    margin: 10px;
}

.bronzeBadge::before {
    content: '';
    height: 10px;
    width: 10px;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 5px;
    left: -10px;
    background: rgb(165, 97, 42);
}

.silverBadge::before {
    content: '';
    height: 10px;
    width: 10px;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 5px;
    left: -10px;
    background: rgb(167, 167, 167);
}

.goldBadge::before {
    content: '';
    height: 10px;
    width: 10px;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 5px;
    left: -10px;
    background: rgb(255, 242, 56);
}